<template>
    <div class="board-detail">
      <div class="common-buttons">
        <button type="button" class="w3-button w3-round w3-blue-gray" v-on:click="fnUpdate">수정</button>&nbsp;
        <button type="button" class="w3-button w3-round w3-red" v-on:click="fnDelete">삭제</button>&nbsp;
        <button type="button" class="w3-button w3-round w3-gray" v-on:click="fnList">목록</button>
      </div>
      <div class="board-contents">
        <h3>{{ title }}</h3>
        <div>
          <strong class="w3-large">{{ author }}</strong>
          <br>
          <span>{{ createdAt }}</span>
        </div>
      </div>
      <div class="board-contents">
        <span>{{ contents }}</span>
      </div>
      <div class="common-buttons">
        <button type="button" class="w3-button w3-round w3-blue-gray" v-on:click="fnUpdate">수정</button>&nbsp;
        <button type="button" class="w3-button w3-round w3-red" v-on:click="fnDelete">삭제</button>&nbsp;
        <button type="button" class="w3-button w3-round w3-gray" v-on:click="fnList">목록</button>
      </div>
    </div>
</template>
  
<script>
  export default {
    data() { //변수생성
      return {
        requestBody: this.$route.query,
        idx: this.$route.query.idx,
  
        title: '',
        author: '',
        contents: '',
        createdAt: ''
      }
    },
    mounted() {
      this.fnGetView()
    },
    methods: {
      fnGetView() {
        this.$axios.get("/board/detail", {
          params: this.requestBody
        }).then((res) => {
            const resData = res.data;
            this.title = resData.data.title
            this.author = resData.data.author
            this.contents = resData.data.contents
            this.createdAt = resData.data.createdAt
        }).catch((err) => {
          if (err.message.indexOf('Network Error') > -1) {
            alert('네트워크가 원활하지 않습니다.\n잠시 후 다시 시도해주세요.')
          }
        })
      },
      fnList() {
        delete this.requestBody.idx
        this.$router.push({
          path: './list',
          query: this.requestBody
        })
      },
      fnUpdate() {
        this.$router.push({
          path: './write',
          query: this.requestBody
        })
      },
      fnDelete() {
        console.log(this.requestBody);
        const delParam = { "idx" : this.requestBody.idx };
        console.log("== delParam : "+delParam);

        const form = new FormData();
        form.append('idx', this.requestBody.idx);

        if (!confirm("삭제하시겠습니까?")) return
  
        this.$axios.post('/board/deleteBoard', delParam).then((res) => {
            const resData = res.data;
            
            alert(resData.data.idx + '가 삭제되었습니다.')
            this.fnList();
          }).catch((err) => {
          console.log(err);
        })
      }
    }
  }
</script>
